<template>
  <div class="content">
    <tab :yearAchtive="yearAchtive" @onclick="selectTime" :data='this.getRespone.tabJson'/>
    <veHistogram :data="jsons" :legend_Visible="false" :settings="historgramSetting" />
    <list :data="this.getRespone.projectClassify" :allPrice="allPrice" :titleLeft="'项目分类'" />

    <!-- <section class="test">
      <van-button type="primary" color="linear-gradient(to right, #4bb0ff, #6149f6)" to="/mine/consume">消费走势</van-button>
      <van-button type="primary" color="linear-gradient(to right, #4bb0ff, #6149f6)" to="/mine/businessAnalyse">营业分析</van-button>
    </section> -->
    <footerBottom :tabbars="this.getRespone.bossTabbars" />
  </div>
</template>

<script>
import tab from "@/components/tab";
import list from "@/components/projectList";
import veHistogram from "@/components/veHistogram";

export default {
  data() {
    this.historgramSetting = {
      yAxisName: ["销售金额(元)"]
    };
    return {
      yearAchtive: 1,
      jsons: this.json,
      allPrice: 0,
    };
  },
  mounted() {
    var columns = ["name", "money"];
    var rows = this.getRespone.projectClassify;
    this.jsons = { columns, rows };
    rows.forEach(element => {
      this.allPrice += element.money;
    });
  },
  methods: {
    selectTime(e) {
      var year = this.util.getTime(new Date()).substring(0, 4);
      var month = this.util.getTime(new Date()).substring(5, 7);
      var day = this.util.getTime(new Date()).substring(8, 10);
      this.yearAchtive = e;
    }
  },
  components: {
    tab,
    list,
    veHistogram
  }
};
</script>

<style lang="scss" scoped>
.content{
  @include wh(100%,calc(100% - 50px));
  overflow: scroll;
}
</style>